.text{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}
.text button{
    margin-left: auto;
    margin-top: 20px;
}
.content{
    padding: 30px;
}

.status{
    margin: 20px 0;
    display: flex;
    align-items: center;
}
.statusSpan{

    display: flex;
    align-items: center;
}
.status span{
    padding: 5px;
}

.message{
    display: flex;
    align-items: center;
    width: 100%;
}
.message p{
    padding: 0 20px 0;
    margin: 0;
    margin-right: auto;
}
.avaBlock{
    display: flex;
}

@media (max-width: 600px) {
    .content{
        padding: 5px;
    }

}