.userCard{
    display: flex;
    flex-direction: column;
    width: 244px;
    margin: 12px;
    border: 1px solid black;
    padding: 16px;
    border-radius: 5px;
    align-items: center;

}
.userWrap{
    display: flex;
    flex-wrap: wrap;
    width: 100% ;
    margin: 0 auto;
    justify-content: center;
    padding: 30px;
}
.paginButton{
    padding: 6px 10px;
    border: 1px solid #282c34;
    border-radius: 3px;
    margin: 0 5px;
    cursor: pointer;
    outline: none;
}
.allU{
    display: flex;
    flex-wrap: wrap;
    align-items: center;

}
.info{
    width: 100% ;
}
.button{
    margin: 30px auto;
}